<template>
  <div class="login-wrap">
    <div class="ms-login">
      <div class="login-title">登录</div>
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        class="demo-ruleForm"
      >
        <el-form-item prop="username" label="用户名">
          <el-input
            v-model="ruleForm.username"
            placeholder="请输入用户名"
          ></el-input>
        </el-form-item>
        <el-form-item prop="password" label="密码"> 
          <el-input
            type="password"
            placeholder="请输入密码"
            v-model="ruleForm.password"
            @keyup.enter.native="submitForm('ruleForm')"
          ></el-input>
        </el-form-item>
        <div class="login-btn">
          <el-button type="success" @click="toRegister">去注册</el-button>
          <el-button type="primary" @click="submitForm">登录</el-button>
        </div>
      </el-form>
    </div>
  </div>
</template>

<script>
import { mixin } from "../mixins";
import { getLoginStatus } from "../api/index";
export default {
  mixins: [mixin],
  data: function () {
    return {
      yztoken: "",
      ruleForm: {
        username: "",
        password: "",
      },
      rules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }],
      },
    };
  },
  methods: {
    submitForm() {
      let params = new URLSearchParams();
      params.append("username", this.ruleForm.username);
      params.append("password", this.ruleForm.password);
      getLoginStatus(params)
        .then((res) => {
          console.log(res);
          if (res.code == 200) {
            window.localStorage.setItem("userInfo", JSON.stringify(res));
            window.localStorage.setItem("userId", res.user_id);
            window.localStorage.setItem("is_login", res.is_login);
            window.localStorage.setItem("token", res.addheader);
			
            this.$router.push("/");
            this.notify("登录成功", "success");
          } else {
            this.notify(res.msg?res.msg:"登录失败", "error");
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    toRegister(){
      this.$router.push("/register")
    }
  },
  created() {},
  mounted() {},
};
</script>

<style scoped>
.login-wrap {
  background: url("../assets/img/logo_bg.webp") no-repeat;
  background-size: 100% 100%;

  width: 100%;
  height: 100%;
}
.ms-logo img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.ms-login {
  position: absolute;
  left: calc(50% - 210px);
  top: 25%;
  width: 300px;
  height: 260px;
  margin: 0 0 0 0;
  padding: 40px;
  border-radius: 15px;
  background: rgba(255, 255, 255, 0.4);
}
.login-btn {
  display: flex;
  justify-content: flex-end;
}
.login-btn button {
  width: 30%;
  height: 36px;
}
.login-title {
  font-size: 24px;
  color: #fff;
  margin-top: -10px;
  padding-bottom: 20px;
  text-align: center;
  font-weight: bold
}
::v-deep .el-form-item__label{
  color: #fff;
}
</style>
